import React, { useEffect, useState, useRef } from 'react';
import axios from 'axios';
import config from 'config';
import s from './Admin.module.scss';
import { Loader } from 'components/comp/Loader/Loader';

const Admin = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editProduct, setEditProduct] = useState(null);
  const [showNewCategoryInput, setShowNewCategoryInput] = useState(false);
  const [newCategoryName, setNewCategoryName] = useState('');
  const [showNewSubcategoryInput, setShowNewSubcategoryInput] = useState(false);
  const [newSubcategoryName, setNewSubcategoryName] = useState('');
  const [formData, setFormData] = useState({
    title: '',
    price: '',
    price2: '',
    src: '',
    text: '',
    description: '',
    zvd: '',
    isNew: false,
    categoryId: '',
    subcategoryId: '',
    category: '',
    subcategory: '',
  });
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');

  useEffect(() => {
    const fetchData = async () => {
      try {
        const { data } = await axios.get(`${config.baseURL}/api/products`);

        //   
        console.log('   API:', data);

        const allProducts = data.flatMap(category =>
          (category.subcategories || []).flatMap(subcategory =>
            (subcategory.items || []).map(item => ({
              ...item,
              category: category.category,
              categoryId: category._id,
              subcategory: subcategory.subcategory,
              subcategoryId: subcategory._id,
            }))
          )
        );

        //     
        console.log(' :', allProducts);

        setProducts(allProducts);
        setCategories(data);
      } catch (error) {
        console.error('   :', error);
        alert('   .');
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();
  }, []);

  const openModal = (product = null) => {
    if (product) {
      setFormData({
        ...product,
        isNew: Boolean(product.isNew),
      });
    } else {
      setFormData({
        title: '',
        price: '',
        price2: '',
        src: '',
        text: '',
        description: '',
        zvd: '',
        isNew: false,
        categoryId: '',
        subcategoryId: '',
        category: '',
        subcategory: '',
      });
    }
    setEditProduct(product);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setEditProduct(null);
    setIsModalOpen(false);
  };

  const handleChange = e => {
    const { name, type, checked, value } = e.target;
    console.log(`Updating ${name}:`, type === 'checkbox' ? checked : value); // 
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value,
    }));
  };

  const handleSubmit = async e => {
    e.preventDefault();

    try {
      const { categoryId, subcategoryId, _id, ...restData } = formData;

      const payload = {
        ...restData,
        categoryId,
        subcategoryId,
      };

      if (editProduct) {
        await axios.put(
          `${config.baseURL}/api/products/category/${editProduct.categoryId}/subcategory/${editProduct.subcategoryId}/product/${_id}`,
          payload
        );
      } else {
        await axios.post(
          `${config.baseURL}/api/products/category/${categoryId}/subcategory/${subcategoryId}/product`,
          payload
        );
      }

      window.location.reload();
    } catch (err) {
      console.error('   ', err);
    }
  };

  const inputFileRef = useRef(null);

  const handleChangeFile = async event => {
    try {
      const file = event.target.files[0];
      const form = new FormData();
      form.append('image', file);

      const { data } = await axios.post(`${config.baseURL}/upload`, form, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });

      setFormData(prev => ({
        ...prev,
        src: data.url, //    src
      }));
    } catch (err) {
      console.warn(err);
      alert('   ');
    }
  };

  const addCategory = async () => {
    if (newCategoryName.trim()) {
      try {
        const { data } = await axios.post(
          `${config.baseURL}/api/products/category`,
          {
            category: newCategoryName.trim(),
            subcategories: [],
          }
        );
        setCategories(prev => [...prev, data]);
        setFormData(prev => ({ ...prev, categoryId: data._id }));
        setShowNewCategoryInput(false);
        setNewCategoryName('');
      } catch (err) {
        console.error('   ', err);
      }
    }
  };

  const addSubcategory = async () => {
    if (newSubcategoryName.trim() && formData.categoryId) {
      try {
        const response = await axios.post(
          `${config.baseURL}/api/products/category/${formData.categoryId}/subcategory`,
          {
            subcategory: newSubcategoryName.trim(),
          }
        );

        const data = response.data;

        //         
        const newSub = {
          _id: data._id,
          subcategory: data.subcategory || newSubcategoryName.trim(),
        };

        const updatedCategories = categories.map(cat =>
          cat._id === formData.categoryId
            ? { ...cat, subcategories: [...cat.subcategories, newSub] }
            : cat
        );

        setCategories(updatedCategories);
        setFormData(prev => ({ ...prev, subcategoryId: newSub._id }));
        setShowNewSubcategoryInput(false);
        setNewSubcategoryName('');
      } catch (err) {
        console.error('   ', err);
      }
    }
  };

  //    
  const handleDelete = async product => {
    try {
      await axios.delete(
        `${config.baseURL}/api/products/category/${product.categoryId}/subcategory/${product.subcategoryId}/product/${product._id}`
      );
      setProducts(prev => prev.filter(p => p._id !== product._id));
    } catch (err) {
      console.error('   ', err);
      alert('   .');
    }
  };

  const filteredProducts = products.filter(product => {
    const matchesSearch = product.title.toLowerCase().includes(searchQuery.toLowerCase());
    const matchesCategory = selectedCategory ? product.categoryId === selectedCategory : true;
    return matchesSearch && matchesCategory;
  });

  return (
    <div className={s.adminWrapper}>
      <div className={s.controlBar}>
        <button onClick={() => openModal()} className={s.createButton}>
           
        </button>

        <input
          type="text"
          placeholder="  "
          value={searchQuery}
          onChange={e => setSearchQuery(e.target.value)}
          className={s.searchInput}
        />

        <select
          value={selectedCategory}
          onChange={e => setSelectedCategory(e.target.value)}
          className={s.categoryFilter}
        >
          <option value=""> </option>
          {categories.map(cat => (
            <option key={cat._id} value={cat._id}>
              {cat.category}
            </option>
          ))}
        </select>
      </div>

      <table className={s.productTable}>
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th> 2</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {isLoading ? (
                  <Loader/>
                ) : (
                  filteredProducts.map((product, index) => (
            <tr key={product._id}>
              <td>
                <img
                  src={`${config.baseURL}${product.src}`}
                  alt={product.title}
                  className={s.tableImage}
                  crossOrigin="anonymous"
                  loading="lazy"
                />
              </td>
              <td>{product.title}</td>
              <td>{product.price}</td>
              <td>{product.price2}</td>
              <td>{product.category}</td>
              <td>{product.subcategory}</td>
              <td>{product.text}</td>
              <td>{product.description}</td>
              <td>{product.zvd}</td>
              <td>{product.isNew ? '' : ''}</td>
              <td>
                <button onClick={() => openModal(product)}></button>
              </td>
              <td>
                <button onClick={() => handleDelete(product)}></button>
              </td>
            </tr>
          ))
                )}
          {/* {filteredProducts.map((product, index) => (
            <tr key={product._id}>
              <td>
                <img
                  src={`${config.baseURL}${product.src}`}
                  alt={product.title}
                  className={s.tableImage}
                  crossOrigin="anonymous"
                  loading="lazy"
                />
              </td>
              <td>{product.title}</td>
              <td>{product.price}</td>
              <td>{product.price2}</td>
              <td>{product.category}</td>
              <td>{product.subcategory}</td>
              <td>{product.text}</td>
              <td>{product.description}</td>
              <td>{product.zvd}</td>
              <td>{product.isNew ? '' : ''}</td>
              <td>
                <button onClick={() => openModal(product)}></button>
              </td>
              <td>
                <button onClick={() => handleDelete(product)}></button>
              </td>
            </tr>
          ))} */}
        </tbody>
      </table>

      {isModalOpen && (
        <div className={s.modalOverlay}>
          <div className={s.modalContent}>
            <button onClick={closeModal} className={s.closeButton}>
              
            </button>
            <form onSubmit={handleSubmit} className={s.modalForm}>
              <select
                name="categoryId"
                value={formData.categoryId}
                onChange={e => {
                  if (e.target.value === 'new') {
                    setShowNewCategoryInput(true);
                    setFormData(prev => ({ ...prev, categoryId: '' }));
                  } else {
                    setShowNewCategoryInput(false);
                    handleChange(e);
                  }
                }}
                required
              >
                <option value=""> </option>
                {categories.map(c => (
                  <option value={c._id} key={c._id}>
                    {c.category}
                  </option>
                ))}
                <option value="new">+   </option>
              </select>

              {showNewCategoryInput && (
                <div className={s.inlineCreate}>
                  <input
                    value={newCategoryName}
                    onChange={e => setNewCategoryName(e.target.value)}
                    placeholder="  "
                  />
                  <button type="button" onClick={addCategory}>
                    
                  </button>
                </div>
              )}

              {formData.categoryId && (
                <>
                  <select
                    name="subcategoryId"
                    value={formData.subcategoryId}
                    onChange={e => {
                      if (e.target.value === 'new') {
                        setShowNewSubcategoryInput(true);
                        setFormData(prev => ({ ...prev, subcategoryId: '' }));
                      } else {
                        setShowNewSubcategoryInput(false);
                        handleChange(e);
                      }
                    }}
                    required
                  >
                    <option value=""> </option>
                    {categories
                      .find(c => c._id === formData.categoryId)
                      ?.subcategories.map(s => (
                        <option value={s._id} key={s._id}>
                          {s.subcategory}
                        </option>
                      ))}
                    <option value="new">+   </option>
                  </select>

                  {showNewSubcategoryInput && (
                    <div className={s.inlineCreate}>
                      <input
                        value={newSubcategoryName}
                        onChange={e => setNewSubcategoryName(e.target.value)}
                        placeholder="  "
                      />
                      <button type="button" onClick={addSubcategory}>
                        
                      </button>
                    </div>
                  )}
                </>
              )}
              <input
                name="title"
                value={formData.title}
                onChange={handleChange}
                placeholder=""
                required
              />
              <input
                name="price"
                value={formData.price}
                onChange={handleChange}
                placeholder=""
                required
              />
              <input
                name="price2"
                value={formData.price2}
                onChange={handleChange}
                placeholder=" 2"
              />
              <label style={{ display: 'none' }}>
                <input
                  name="src"
                  value={formData.src}
                  onChange={handleChange}
                  placeholder="URL "
                />
              </label>

              <input
                ref={inputFileRef}
                type="file"
                onChange={handleChangeFile}
                hidden
              />

              <button
                type="button"
                onClick={() => inputFileRef.current.click()}
              >
                 
              </button>

              {formData.src && (
                <img
                  src={`${config.baseURL}${formData.src}`}
                  alt=""
                  style={{ width: '100px', marginTop: '10px' }}
                  crossOrigin="anonymous"
                  loading="lazy"
                />
              )}

              <input
                name="text"
                value={formData.text}
                onChange={handleChange}
                placeholder=""
              />
              <textarea
                name="description"
                value={formData.description}
                onChange={handleChange}
                placeholder=""
              />
              <input
                name="zvd"
                value={formData.zvd}
                onChange={handleChange}
                placeholder=""
              />
              <label>
                 
                <input
                  type="checkbox"
                  name="isNew"
                  checked={formData.isNew}
                  onChange={handleChange}
                />
              </label>
              <button type="submit"></button>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Admin;
